import { useState } from "react";
import "../App.css";
import FormInput from "./FormInput";
import { Link, useNavigate } from "react-router-dom";
import { createUserWithEmailAndPassword, updateProfile } from "firebase/auth";
import { auth } from "../firebase";
import { DatePicker } from "antd";
import "antd/dist/antd.css"; // Css file of ant picker library
import country from "./countryList";
import { address } from "./inputFields";
import { nepState, indiaState } from "./State";
import { Facebook, Google, Twitter } from "@mui/icons-material";
const SignUp = () => {
  const navigate = useNavigate();
  const [select, setSelect] = useState("");
  const [values, setValues] = useState({
    fname: "",
    lname: "",
    email: "",
    birthday: "",
    password: "",
    confirmPassword: "",
    street1: "",
    street2: "",
    phone: "",
    city: "",
    zipcode: "",
    country: "",
    state: "",
  });
  const [submitDisable, setSubmitDisable] = useState(false);

  const [errorMsg, setErrorMsg] = useState("");
  const inputs = [
    {
      id: 1,
      name: "fname",
      type: "text",
      placeholder: "First Name",
      errorMessage:
        "First Name should be 3-16 characters and shouldn't include any special character!",
      label: "First Name*",
      pattern: "^[A-Za-z0-9]{3,16}$",
      required: true,
      autoComplete: "off",
    },

    {
      id: 2,
      name: "lname",
      type: "text",
      placeholder: "Last Name",
      errorMessage:
        "Last Name should be 3-16 characters and shouldn't include any special character!",
      label: "Last Name*",
      pattern: "^[A-Za-z0-9]{3,16}$",
      required: true,
      autoComplete: "off",
    },

    {
      id: 3,
      name: "email",
      type: "email",
      placeholder: "Email",
      errorMessage: "It should be a valid email address!",
      pattern: "[a-z0-9._%+-]+@[a-z0-9.-]+.[a-z]{2,4}$",
      label: "Email*",
      required: true,
      autoComplete: "off",
    },
    {
      id: 4,
      name: "phone",
      type: "number",
      placeholder: "Phone Number",
      errorMessage: "This field is required",
      label: "Phone Number*",
      pattern: "",
      required: true,
      autoComplete: "off",
    },
  ];
  const inputss = [
    {
      id: 5,
      name: "password",
      type: "password",
      placeholder: "Password",
      errorMessage:
        "Password should be 8-20 characters and include at least 1 letter, 1 number and 1 special character!",
      label: "Password*",
      pattern: `^(?=.*[0-9])(?=.*[a-zA-Z])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{8,20}$`,
      required: true,
      autoComplete: "off",
    },
    {
      id: 6,
      name: "confirmPassword",
      type: "password",
      placeholder: "Confirm Password",
      errorMessage: "Password don't Match!",
      label: "Confirm Password*",
      pattern: values.password,
      required: true,
      autoComplete: "off",
    },
  ];
  const handleSubmit = (e) => {
    e.preventDefault();
    const {
      fname,
      lname,
      email,
      birthday,
      password,
      street1,
      street2,
      phone,
      city,
      zipcode,
      country,
      state,
    } = values;
    if (
      fname &&
      lname &&
      email &&
      birthday &&
      password &&
      street1 &&
      street2 &&
      phone &&
      city &&
      zipcode &&
      country
    ) {
      const res = fetch(
        "https://validation-a2b6e-default-rtdb.firebaseio.com/Register_Records.json",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            fname,
            lname,
            email,
            birthday,
            password,
            street1,
            street2,
            phone,
            city,
            zipcode,
            country,
            state,
          }),
        }
      );
      if (res) {
        handleSubmission();
      }
    }
  };

  const onChange = (e) => {
    setValues({ ...values, [e.target.name]: e.target.value });
  };

  const handleSubmission = () => {
    setErrorMsg("");
    if (
      !values.fname ||
      !values.email ||
      !values.password ||
      !values.lname ||
      !values.birthday ||
      !values.confirmPassword ||
      !values.street1 ||
      !values.street2 ||
      !values.city ||
      !values.phone ||
      !values.country ||
      !values.zipcode
    ) {
      setErrorMsg("Please Filled the Required Fields");
      return;
    }

    setErrorMsg("");
    setSubmitDisable(true);

    createUserWithEmailAndPassword(auth, values.email, values.password)
      .then(async (res) => {
        setSubmitDisable(false);
        const user = res.user;
        await updateProfile(user, {
          displayName: values.fname,
        });
        navigate("/");
      })
      .catch((err) => {
        setSubmitDisable(false);

        if (err.message === "Firebase: Error (auth/user-not-found).") {
          setErrorMsg("User Not Found");
        } else if (err.message === "Firebase: Error (auth/invalid-email).") {
          setErrorMsg("Invalid Email");
        }
      });
  };

  const handleCapacity = (e) => {
    const getuser = e.target.value;
    setSelect(getuser);
  };
  return (
    <div className="Apps">
      <div className="app">
        <div className="center">
          <form onSubmit={handleSubmit}>
            <h1>Register </h1>
            {inputs.map((input) => (
              <FormInput
                className="forminput1"
                key={input.id}
                {...input}
                value={values[input.name]}
                onChange={onChange}
                style={values[input.style]}
              />
            ))}
            <label htmlFor="birthday">Date of Birth*</label>
            <DatePicker
              name="birthday"
              placeholder="Date of Birth"
              onBlur={onChange}
            />
            {inputss.map((curElem) => (
              <FormInput
                className="forminput1"
                key={curElem.id}
                {...curElem}
                value={values[curElem.name]}
                onChange={onChange}
              />
            ))}
            <hr />
            <hr />
            <h1 style={{ fontSize: "20px" }}>Address</h1>
            <label htmlFor="country">Country*</label>
            <select
              name="country"
              id="country"
              onBlur={onChange}
              onChange={handleCapacity}
            >
              {country.map((curElement) => (
                <>
                  <option>{curElement.name}</option>
                </>
              ))}
            </select>
            <label htmlFor="states">State</label>
            <div name="states">
              {select === "Nepal" && (
                <select id="nepState" name="state" onBlur={onChange}>
                  {nepState.map((curElem) => (
                    <option>{curElem.state}</option>
                  ))}
                </select>
              )}
              {select === "India" && (
                <select id="indState" name="state" onBlur={onChange}>
                  {indiaState.map((curElem) => (
                    <option>{curElem.state}</option>
                  ))}
                </select>
              )}
              {select !== "India" && select !== "Nepal" && (
                <FormInput
                  placeholder="Enter Your State..."
                  name="state"
                  autoComplete="off "
                  value={values.otherState}
                  onChange={onChange}
                />
              )}
            </div>
            <div className="half">
              {address.map((curE) => (
                <div className="gridd">
                  <FormInput
                    className="gridd"
                    variant="outlined"
                    key={curE.id}
                    {...curE}
                    value={values[curE.name]}
                    onChange={onChange}
                  />
                </div>
              ))}
            </div>
            <b className="error">{errorMsg}</b>
            <button disabled={submitDisable} className="submit-btn">
              Submit
            </button>
            <div className="login">
              <p to="" className="loginss">
                SignUp With
              </p>
              <Link to="/fblogin" className="fblogin">
                <Facebook />
              </Link>
              <Link to="/tlogin" className="fblogin">
                <Twitter />
              </Link>
              <Link to="/glogin" className="fblogin">
                <Google />
              </Link>
            </div>
            <Link to="/" className="link">
              Already Have An Account
            </Link>
          </form>
        </div>
      </div>
    </div>
  );
};

export default SignUp;
