import { useState } from "react";
import "../App.css";
import FormInput from "./FormInput";
import { Link } from "react-router-dom";
import { auth } from "../firebase";
import { signInWithEmailAndPassword } from "firebase/auth";
import { useNavigate } from "react-router-dom";
const SignIn = () => {
  // Hook for Error message
  const [errorMsg, setErrorMsg] = useState("");
  // Hook for Error message End

  // useState Hook for button disable while submitting
  const [submitButtonDisabled, setSubmitButtonDisabled] = useState(false);
  // useState Hook for button disable while submitting End

  // Setting Initial value of input field
  const [values, setValues] = useState({
    email: "",
    password: "",
  });
  // Setting Initial value of input field End

  // Hook for navigate
  const navigate = useNavigate();
  // Hook for navigate End

  // Inputs Property for form
  const inputs = [
    {
      id: 1,
      name: "email",
      type: "email",
      placeholder: "Email",
      errorMessage: "It should be a valid email address!",
      label: "Email*",
      required: true,
      autoComplete: "off",
    },

    {
      id: 2,
      name: "password",
      type: "password",
      placeholder: "Password",
      errorMessage:
        "Password should be 8-20 characters and include at least 1 letter, 1 number and 1 special character!",
      label: "Password*",
      pattern: `^(?=.*[0-9])(?=.*[a-zA-Z])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{8,20}$`,
      required: true,
      autoComplete: "off",
    },
  ];
  // Inputs Property for form End

  const handleSubmit = (e) => {
    e.preventDefault();
  };

  const onChange = (e) => {
    setValues({ ...values, [e.target.name]: e.target.value });
  };
  // Showing Error message and button dissable on form submitting
  const handleSubmission = () => {
    if (!values.email || !values.password) {
      setErrorMsg("");
      return;
    }

    setSubmitButtonDisabled(true);
    // SignIn user with email and password from firebase authentication
    signInWithEmailAndPassword(auth, values.email, values.password)
      .then(async (res) => {
        setSubmitButtonDisabled(false);
        // Navigate to home page after login if login credential is correct
        navigate("/home");
        // Navigate to home page after login if login credential is correct End
      })

      // Converting Firebase Error to Custom Error
      .catch((err) => {
        setSubmitButtonDisabled(false);
        if (err.message === "Firebase: Error (auth/user-not-found).") {
          setErrorMsg("User Not Found");
        } else if (err.message === "Firebase: Error (auth/invalid-email).") {
          setErrorMsg("Invalid Email");
        } else if (err.message === "Firebase: Error (auth/wrong-password).") {
          setErrorMsg("Wrong Password");
        }
        // Converting Firebase Error to Custom Error End
      });
    // SignIn user with email and password from firebase authentication End
  };
  // Showing Error message and button dissable on form submitting End

  return (
    <div className="Apps">
      <div className="app">
        <div className="center">
          <form onSubmit={handleSubmit}>
            <h1>Sign In </h1>
            {/* Map method to showing input field */}
            {inputs.map((input) => (
              <FormInput
                key={input.id}
                {...input}
                value={values[input.name]}
                onChange={onChange}
              />
            ))}
            {/* Map method to showing input field End*/}

            <b className="error">{errorMsg}</b>
            {/* Button for Submit Data */}
            <button
              onClick={handleSubmission}
              className="submit-btn"
              disabled={submitButtonDisabled}
            >
              Log In
            </button>
            {/* Button for Submit Data */}

            {/* Navigate user to signup page on link click */}
            <Link to="/SignUp" className="link">
              Register Account
            </Link>
            {/* Navigate user to signup page on link click End*/}
          </form>
        </div>
      </div>
    </div>
  );
};

export default SignIn;
